<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择公司"
              @change="onChangeSearchCompany"
              clearable
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              @change="onChangeSearchSpot"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 输入手续费 start -->
          <el-form-item label="" prop="charge">
            <el-input
              class="input-name"
              v-model="searchValidateForm.charge"
              placeholder="请输入手续费(%)"
            ></el-input>
          </el-form-item>
          <!-- 输入手续费 end -->

          <!-- 输入税点 start -->
          <el-form-item label="" prop="tax">
            <el-input
              class="input-name"
              v-model="searchValidateForm.tax"
              placeholder="请输入税点(%)"
            ></el-input>
          </el-form-item>
          <!-- 输入税点 end -->

          <!-- 合同有效期 start -->
          <el-form-item label="" prop="contractValidity">
            <el-date-picker
              v-model="searchContractValidity"
              type="daterange"
              start-placeholder="开始合同有效期"
              end-placeholder="结束合同有效期"
              style="width: 100%"
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 合同有效期 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 导出 Excel end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ setTableValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
