          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle + '签约信息'"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 start -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm-signinfo"
          :rules="validateFormRules"
        >
          <!-- 合作公司名称 start -->
          <!-- <el-form-item label="合作公司名称" prop="companyName">
            <el-input type="text" v-model="validateForm.companyName" autocomplete="off" placeholder="请输入合作公司名称"></el-input>
          </el-form-item> -->
          <el-form-item label="合作公司名称" prop="companyName">
            <el-select
              v-model="validateForm.companyName"
              filterable
              placeholder="请选择公司"
              style="width: 100%"
              @change="onChangeCompany"
            >
              <el-option
                v-for="(item, index) in companyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_NAME"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 合作公司名称 end -->

          <!-- 景区承担配件费用比例 start -->
          <el-form-item
            label="景区承担配件费用比例(%)"
            prop="spotAccessoryRatio"
          >
            <el-input
              type="text"
              v-model="validateForm.spotAccessoryRatio"
