              placeholder="请输入景区承担配件费用比例"
            ></el-input>
          </el-form-item>
          <!-- 景区承担配件费用比例 end -->

          <!-- 景区承担维修费用比例 start -->
          <el-form-item label="景区承担维修费用比例(%)" prop="spotRepairRatio">
            <el-input
              type="text"
              v-model="validateForm.spotRepairRatio"
              autocomplete="off"
              placeholder="请输入景区承担维修费用比例"
            ></el-input>
          </el-form-item>
          <!-- 景区承担维修费用比例 end -->

          <!-- 景区名称 start -->
          <el-form-item label="景区名称" prop="spotName">
            <el-select
              v-model="validateForm.spotName"
              filterable
              placeholder="请选择景区"
              style="width: 100%"
