              </el-option>
            </el-select>
          </el-form-item>
          <!-- 景区名称 end -->

          <!-- 税率公式 start -->
          <el-form-item label="税率公式" prop="taxRateMethod">
            <el-select
              v-model="validateForm.taxRateMethod"
              filterable
              placeholder="请选择税率公式"
              style="width: 100%"
