              </el-option>
            </el-select>
          </el-form-item>
          <!-- 税率公式 end -->

          <!-- 手续费 start -->
          <el-form-item label="手续费(%)" prop="charge">
            <el-input
              type="text"
              v-model="validateForm.charge"
              autocomplete="off"
              placeholder="请输入手续费"
            ></el-input>
          </el-form-item>
          <!-- 手续费 end -->

          <!-- 税点 start -->
          <el-form-item label="税点(%)" prop="tax">
            <el-input
              type="text"
              v-model="validateForm.tax"
              autocomplete="off"
              placeholder="请输入税点"
            ></el-input>
          </el-form-item>
          <!-- 税点 end -->

          <!-- 分润比例 start -->
          <el-form-item label="分润比例(%)" prop="proportion">
            <el-input
              type="text"
              v-model="validateForm.proportion"
              autocomplete="off"
              placeholder="请输入分润比例"
            ></el-input>
          </el-form-item>
          <!-- 分润比例 end -->

          <!-- 收支类型 start -->
          <el-form-item label="收支类型" prop="revenueExpenditure">
            <el-select
              v-model="validateForm.revenueExpenditure"
              filterable
              placeholder="请选择收支类型"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in revenueExpenditureOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 收支类型 end -->

          <!-- 发票类型 start -->
          <el-form-item label="发票类型" prop="invoiceType">
            <el-select
              v-model="validateForm.invoiceType"
              filterable
              placeholder="请选择发票类型"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in invoiceTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 发票类型 end -->

          <!-- 微信支付 start -->
          <el-form-item label="微信支付" prop="wechat">
            <el-select
              v-model="validateForm.wechat"
              filterable
              placeholder="是否为微信支付"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in wechatOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 微信支付 end -->

          <!-- 储值支付 start -->
          <el-form-item label="储值支付" prop="saving">
            <el-select
              v-model="validateForm.saving"
              filterable
              placeholder="是否为储值支付"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in savingOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 储值支付 end -->

          <!-- 储值抵扣+微信支付 start -->
          <el-form-item label="储值抵扣+微信支付" prop="savingWechat">
            <el-select
              v-model="validateForm.savingWechat"
              filterable
              placeholder="是否为储值抵扣+微信支付"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in savingWechatOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 储值抵扣+微信支付 end -->

          <!-- 押金抵扣 start -->
          <el-form-item label="押金抵扣" prop="deposit">
            <el-select
              v-model="validateForm.deposit"
              filterable
              placeholder="是否为押金抵扣"
              style="width: 100%"
            >
              <el-option
                v-for="(item, index) in depositOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 押金抵扣 end -->

          <!-- 合同编号 start -->
          <el-form-item label="合同编号" prop="contractNumber">
            <el-input
              type="text"
              v-model.number="validateForm.contractNumber"
              autocomplete="off"
              placeholder="请输入合同编号"
            ></el-input>
          </el-form-item>
          <!-- 合同编号 end -->

          <!-- 合同有效期 start -->
          <el-form-item label="合同有效期" prop="contractValidity">
            <!-- <el-input type="text" v-model="validateForm.contractValidity" autocomplete="off" placeholder="请输入合同有效期"></el-input> -->
            <el-date-picker
              v-model="validateForm.contractValidity"
              type="date"
              placeholder="请选择合同有效期"
              style="width: 100%"
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 合同有效期 end -->

          <!-- 签约日期 start -->
          <el-form-item label="签约日期" prop="contractSignDate">
            <!-- <el-input type="text" v-model="validateForm.contractSignDate" autocomplete="off" placeholder="请输入签约日期"></el-input> -->
            <el-date-picker
              v-model="validateForm.contractSignDate"
              type="date"
              placeholder="请选择签约日期"
              style="width: 100%"
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 签约日期 end -->

          <!-- 次月对账日 start -->
          <el-form-item label="次月对账日" prop="reconciliationDate">
            <el-input
              type="text"
              v-model="validateForm.reconciliationDate"
              autocomplete="off"
              placeholder="请输入次月对账日"
            ></el-input>
            <!-- <el-date-picker
              v-model="validateForm.reconciliationDate"
              type="date"
              placeholder="请选择次月对账日"
              style="width: 100%"
              value-format="yyyy-MM-dd"
              popper-class="reconciliation-date"
            >
            </el-date-picker> -->
          </el-form-item>
          <!-- 次月对账日 end -->

          <!-- 次月结算日 start -->
          <el-form-item label="次月结算日" prop="settlement">
            <el-input
              type="text"
              v-model="validateForm.settlement"
              autocomplete="off"
              placeholder="请输入次月结算日"
            ></el-input>
            <!-- <el-date-picker
              v-model="validateForm.settlement"
              type="date"
              placeholder="请选择次月结算日"
              style="width: 100%"
              value-format="yyyy-MM-dd"
              popper-class="settlement"
            >
            </el-date-picker> -->
          </el-form-item>
          <!-- 次月结算日 end -->

          <!-- 合作主体 start -->
          <!-- <el-form-item label="合作主体" prop="subjectName">
            <el-input type="text" v-model="validateForm.subjectName" autocomplete="off" placeholder="请输入合作主体"></el-input>
          </el-form-item> -->
          <el-form-item label="合作主体" prop="subjectName">
            <el-select
              v-model="validateForm.subjectName"
              filterable
              placeholder="请选择合作主体"
              style="width: 100%"
              @change="onChangeSubject"
            >
              <el-option
                v-for="(item, index) in subjectOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_NAME"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 合作主体 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 end -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'SignInformation',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value && value !== 0) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证1-30数字值)
     * @params message - 提示信息
     */
    function checkDateNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value && value !== 0) {
          return callback()
        }

        if (isNaN(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          if (value >= 1 && value <= 30) {
            callback()
          } else {
            callback(new Error(name + '只能输入1-30内的数字值'))
          }
        }
      }
    }

    return {
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点

      tableLoading: false,
      tableFields: {
        //导出Excel表格的表头设置
        // "合作公司id": "companyId",
        合作公司名称: 'companyName',
        // "景区id": "spotId",
        景区承担配件费用比例: 'spotAccessoryRatio',
        景区承担维修费用比例: 'spotRepairRatio',
        景区名称: 'spotName',
        税率公式: 'taxRateMethodName',
        手续费: 'charge',
        税点: 'tax',
        分润比例: 'proportion',
        收支类型: 'revenueExpenditure',
        发票类型: 'invoiceType',
        微信支付: 'wechat',
        储值支付: 'saving',
        '储值抵扣+微信支付': 'savingWechat',
        押金抵扣: 'deposit',
        合同编号: 'contractNumber',
        合同有效期: 'contractValidity',
        签约日期: 'contractSignDate',
        对账日: 'reconciliationDate',
        结算日: 'settlement',
        // "合作主体id": "subjectId",
        合作主体: 'subjectName',
        备注: 'notes'
      },
      tableData: [], // 表格数据

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      companyOptions: [], // 新增编辑的公司选项
      scenicOptions: [], // 新增编辑的景区选项
      subjectOptions: [], // 新增编辑的合作主体选项
      taxRateOptions: [], // 税率公司选项
      revenueExpenditureOptions: [
        {
          label: '收入',
          value: 1
        },
        {
          label: '支出',
          value: 2
        }
      ], // 收支类型选项
      invoiceTypeOptions: [
        {
          label: '无发票',
          value: 1
        },
        {
          label: '增值税普通发票',
          value: 2
        },
        {
          label: '增值税专用发票',
          value: 3
        }
      ], // 发票类型选项
      wechatOptions: [
        {
          label: '是',
          value: 1
        },
        {
          label: '否',
          value: 2
        }
      ], // 微信支付选项
      savingOptions: [
        {
          label: '是',
          value: 1
        },
        {
          label: '否',
          value: 2
        }
      ], // 储值支付选项
      savingWechatOptions: [
        {
          label: '是',
          value: 1
        },
        {
          label: '否',
          value: 2
        }
      ], // 储值抵扣+微信支付选项
      depositOptions: [
        {
          label: '是',
          value: 1
        },
        {
          label: '否',
          value: 2
        }
      ], // 押金抵扣选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框
      dialogTitle: '', // dialog标题

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      // 搜索需要的表单
      searchValidateForm: {}, // 搜索表单验证对象
      validateForm: {}, // 表单验证对象

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

