    /**
     * 初始化表格数据
     */
    async initTableData() {
      var charge = this.searchValidateForm.charge
      if (charge == '' || !charge) {
        charge = null
      } else {
        charge /= 100
      }

      var tax = this.searchValidateForm.tax
      if (tax == '' || !tax) {
        tax = null
      } else {
        tax /= 100
      }

      this.tableLoading = true
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/list',
        {
          params: {
            ...this.searchValidateForm,
            beginDate:
              this.searchContractValidity && this.searchContractValidity[0],
            endDate:
              this.searchContractValidity && this.searchContractValidity[1],
            charge: charge,
            tax: tax,
            pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res);
      this.tableData = res.data
      this.total = res.total

      this.tableLoading = false
    },

