     */
    async addTableData(data) {
      // console.log(this.validateForm.charge);
      // console.log(this.validateForm.tax);
      // console.log(this.validateForm.proportion);
      const { data: res } = await this.$http.post(
        '/system/subscriptionInformation/add',
