      if (tax == '' || !tax) {
        tax = null
      } else {
        tax /= 100
      }

      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/list',
        {
          params: {
            ...this.searchValidateForm,
            beginDate:
              this.searchContractValidity && this.searchContractValidity[0],
            endDate:
              this.searchContractValidity && this.searchContractValidity[1],
            charge: charge,
            tax: tax
          }
        }
      )
      if (res.state == '200') {
        this.exportTableData = res.data
      } else {
