    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true

          // 请求全部数据
          await this.getExportTableData()
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          this.$nextTick(() => {
            var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
              document.querySelector('#outTable'),
              xlsxParam
            )
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
              bookType: 'xlsx',
              bookSST: true,
              type: 'array'
            })
            try {
              FileSaver.saveAs(
                //Blob 对象表示一个不可变、原始数据的类文件对象。
                //Blob 表示的不一定是JavaScript原生格式的数据。
                //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                new Blob([wbout], {
                  type: 'application/octet-stream'
                }),
                //设置导出文件名称
                '签约信息' + '.xlsx'
              )
            } catch (e) {
              if (typeof console !== 'undefined') console.log(e, wbout)
            }
            this.downloadLoading = false
            return wbout
          })

          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogState = 'add'
      this.dialogTitle = '新增'

      // 设置手续费和税点默认值
      this.validateForm.charge = '0.6'
      this.validateForm.tax = '6'
      this.validateForm.invoiceType = 2
      this.validateForm.wechat = 1
      this.validateForm.saving = 2
      this.validateForm.savingWechat = 2
      this.validateForm.deposit = 2

      this.dialogVisible = true
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);

      var data = { ...row } // 先深拷贝一波data，防止数据错乱
      data.charge *= 100
      data.tax *= 100
      data.proportion *= 100

      // console.log(data);

      this.validateForm = data // 这里直接使用row作为表单验证对象，当修改validateForm某项内容时，会同时影响row表格当前行内容
      this.dialogState = 'edit'
      this.dialogTitle = '编辑'

      this.dialogVisible = true // 是否显示编辑dialog对话框
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.validateForm);
          var data = Object.assign({}, this.validateForm) // 防止数据错乱先深拷贝一波
          data.charge = data.charge / 100
          data.tax = data.tax / 100
          data.proportion = data.proportion / 100

          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData(data)
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData(data)
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    setTableValue() {
      return (row, value) => {
        if (value == 'spotAccessoryRatio' || value == 'spotRepairRatio') {
          var result = row[value] + '%'

          return result
        } else if (
          value == 'charge' ||
          value == 'tax' ||
          value == 'proportion'
        ) {
          // 如果当前列为手续费|税点|分润比例
          var result = row[value] * 100 + '%'

          return result
        } else if (value == 'revenueExpenditure') {
          // 如果当前列为收支类型
          var result = ''
          if (row[value] == 1) {
            result = '收入'
          } else if (row[value] == 2) {
            result = '支出'
          }
          return result
        } else if (value == 'invoiceType') {
          // 如果当前列为发票类型
          var result = ''
          if (row[value] == 1) {
            result = '无发票'
          } else if (row[value] == 2) {
            result = '增值税普通发票'
          } else if (row[value] == 3) {
            result = '增值税专用发票'
          }
          return result
        } else if (value == 'wechat') {
          // 如果当前列为微信支付
          var result = ''
          if (row[value] == 1) {
            result = '是'
          } else if (row[value] == 2) {
            result = '否'
          }
          return result
        } else if (value == 'saving') {
          // 如果当前列为储值支付
          var result = ''
          if (row[value] == 1) {
            result = '是'
          } else if (row[value] == 2) {
            result = '否'
          }
          return result
        } else if (value == 'savingWechat') {
          // 如果当前列为储值抵扣+微信支付
          var result = ''
          if (row[value] == 1) {
            result = '是'
          } else if (row[value] == 2) {
            result = '否'
          }
          return result
        } else if (value == 'deposit') {
          // 如果当前列为押金抵扣
          var result = ''
          if (row[value] == 1) {
            result = '是'
          } else if (row[value] == 2) {
            result = '否'
          }
          return result
        } else if (value == 'reconciliationDate' || value == 'settlement') {
          // 如果当前列为对账日
          var result = `次月${row[value]}日`
          return result
        } else {
          // 否则返回默认数据
          return row[value] || '无'
        }
      }
    }
  }
}
</script>

